import axios from 'axios'
import {JWT} from '../const/const'

let sessionId = undefined // session, session

//  axios 
const instance = axios.create({
  timeout: 60 * 1000 // 
})

// 
instance.interceptors.request.use(
  (config) => {
    console.log('config', config)
    // 
    console.log('config.request', config.request)
    console.log('Sending request to:', config.url)
    console.log('config.data', config.data)
    // sessionId
    if (sessionId) {
      config.data['session_id'] = sessionId
    }
    //  Authorization 
    //  params
    // config.request.config.headers.Authorization = JWT
    config.params = {authentication: JWT}
    return config
  },
  (error) => {
    // 
    return Promise.reject(error)
  }
)

// 
instance.interceptors.response.use(
  (response) => {
    // 
    console.log('Received response from:', response.config.url)
    console.log('data', response.data)
    try {
      const {data} = response
      if (data.session_id) {
        sessionId = data.session_id
      }
      // sessionId,
      return response.data
    } catch (e) {
      // fixme 
      throw new Error('response error')
    }
  },
  (error) => {
    // 
    if (error.response) {
      //  2xx 
      console.error('Error status:', error.response.status)
      console.error('Error data:', error.response.data)
    } else if (error.request) {
      // 
      console.error('No response received:', error.request)
    } else {
      // 
      console.error('Error:', error.message)
    }
    return Promise.reject(error)
  }
)
const errorMap = {
  400: '',
  401: '',
  399: ''
}

export default instance
